import React from 'react';
import styles from './modal.module.scss';
import {createPortal} from 'react-dom';

class Modal extends React.PureComponent{


    state = {IsClose:false}
    closeRef = React.createRef(); 
   

    onCloseModal(){
       this.closeRef.current.style.display="none";
    }

    render(){
        const {isOpen,identity,money,transferDate} =this.props;
       
        return createPortal(
            
            <div className={styles.modal} 
            style={{display:(isOpen)?"block":"none"}}
            ref={this.closeRef} >
                <div  class="modal-content" className={styles.modalContent} >
                    <span className={styles.close} 
                    onClick={this.onCloseModal.bind(this)} > &times;</span>                    
                    <h3>Thank you!!!{identity}大大</h3>
                    <h3>捐贈了{money}元!!!謝謝您!!!</h3>
                </div>
            </div>            ,
            document.body
        )
    }

}

export default Modal;

