import React from 'react';
import styles from './styles.module.scss';
import VideoPlayer from '../VideoPlayer';
import Modal from '../Modal';
import DayPicker from 'react-day-picker';
import DayPickerInput from 'react-day-picker/DayPickerInput';
import 'react-day-picker/lib/style.css';

class MianArea extends React.Component{
  
  constructor(props){
    super(props);
    this. state = {
      contact:'',
      identity:'',
      money:1000,
      paymentWay:'',
      accountNumber:'',
      transferDate:'',
      isOpen:false
    } 

    this.baseState = this.state;
  }

 
 
  handleSubmit(e){

    var formData = new FormData();
    //const url = "https://script.google.com/macros/s/AKfycby3mVS3HqnkSUBsS7nDZ_aeLvKCFexK55rn8GoKww/exec";
    const url = "https://script.google.com/macros/s/AKfycbyMx24UetxxPSjboi8lRlrYVzC1v-Zec6yKQWZC_TKziZOOoNwb/exec";
    formData.append("contact", this.state.contact);
    formData.append("identity", this.state.identity);
    formData.append("money", this.state.money);
    formData.append("paymentWay", this.state.paymentWay);
    formData.append("accountNumber", this.state.accountNumber);
    formData.append("transferDate", this.formatDate(this.state.transferDate));
    
    fetch(url, {
      method: 'POST',
      body: formData
    })
    .then(response => response.json())
    //.then(response => console.log('Success:', JSON.stringify(response)))
    .then(respone =>{
      this.setState({isOpen:true});   
    })
    .catch(error => console.error('Error:', error));

    //this.setState(this.baseState);
    e.preventDefault();
}
  formatDate(d){
    const DateStr = [d.getFullYear(),d.getMonth()+1, d.getDate()].join('/');
    return DateStr;

  }


  onOptionChange(event){
   
    if(event.target.name ==='radio-contact'){
      this.setState({contact:event.target.value});
    }else if(event.target.name ==='pay-way')
    {
      this.setState({paymentWay:event.target.value});
    }    
  };

  render(){ 
    
    return(
          
        <section>
          <Modal            
            identity={this.state.identity}
            money={this.state.money}
            paymentWay={this.state.paymentWay}
            accountNumber={this.state.accountNumber}
            transferDate= {this.state.transferDate}
            isOpen={this.state.isOpen}       
          />  
          <div className={styles.mains}>
            
                <div id="video-background" className={styles.playMV}>
                     <VideoPlayer/> 
                </div>
                <div id="letters" className={styles.letters}>
                <p>武藤十夢 <span>~AKB48 in Taipei 2019 ~ Are You Ready For It?    ~ 台北小巨蛋演唱會 應援募款計畫</span></p>
                </div>
                <div  id ="form-content" className={styles.formContent}>
                  <div id="description" className = {styles.description}>
                    募款宗旨: 本募款計畫為供 2019.10.19 ，AKB48台北演唱會時為成員 
                    <span>武藤十夢</span> 
                    購置應援使用的<span>花籃、禮物、製作應援周邊</span>使用。
                    募款金額目標為新台幣15000元。多餘金額將優先提供給ＡＫＢ48台北演唱會其他贊助活動使用，或是捐出。
                  </div>
                  {/* </div><form id="post_form" action="https://script.google.com/macros/s/AKfycby3mVS3HqnkSUBsS7nDZ_aeLvKCFexK55rn8GoKww/exec" method="post" > */}
                 <form>
                  <div class="form-group row">
                      <label  class="col-3 col-form-label text-right">聯絡方式</label>
                      <div class="col-8">               
                      <div class="form-check form-check-inline">
                        <input class="form-check-input"  type="radio" name="radio-contact" id="line-contact" value='line'
                          checked={this.state.contact==='line' || this.state.contact===''} onChange={this.onOptionChange.bind(this)}/>
                        <label class="form-check-label" for="line-contact">Line</label>
                      </div>
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="radio-contact" id="facebook-contact" value="facebook"
                        checked={this.state.contact==='facebook'} onChange={this.onOptionChange.bind(this)}/>
                        <label class="form-check-label" for="facebook-contact">Face book</label>
                      </div>
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="radio-contact" id="twitter-contact" value="twitter"
                        checked={this.state.contact==='twitter'} onChange={this.onOptionChange.bind(this)}/>
                        <label class="form-check-label" for="twitter-contact">Twitter</label>
                      </div>
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="radio-contact"  id="mail-contact" value="mail"
                        checked={this.state.contact==='mail'} onChange={this.onOptionChange.bind(this)} />
                        <label class="form-check-label" for="mail-contact">Mail</label>
                      </div>
                      </div>
                      </div>
                    <div class="form-group row">
                    <label for="nickName" class="col-3 col-form-label text-right">聯絡ID</label> 
                      <div class="col-8">
                       <input type="text" readonly class="form-control" id="nickName" placeholder="@orin_muto" 
                        value={this.state.identity} 
                        onChange={e=>this.setState({identity:e.target.value})}
                        />
                      </div>
                    </div>  
                    <div class="form-group row">
                      <label for="inputPassword" class="col-3 col-form-label text-right">金額</label>
                         <div class="col-8">
                          <input type="text" class="form-control" id="inputPassword" placeholder="3000" 
                          value={this.state.money} 
                          onChange={
                            (e)=>{
                              if(e.target.value.length>5){alert('你確定要捐這麼多嗎？！');return false;}
                              this.setState({money:e.target.value.replace(/[^0-9]+/g, "")})}
                        }
                          />
                        </div>
                     </div>
                     <div class="form-group row">
                      <label for="inputPassword" class="col-3 col-form-label text-right">捐款方式</label>
                      <div class="col-8">
                      <div class="form-check form-check-inline">
                        <input class="form-check-input" type="radio" name="pay-way"  id="transfer-pay" value="Bank" 
                         checked={this.state.paymentWay==='Bank' || this.state.contact===''} 
                         onChange={this.onOptionChange.bind(this)}
                         />
                        <label class="form-check-label" for="transfer-pay">銀行轉帳(TW)</label>
                      </div>
                      {/* <div class="form-check form-check-inline">
                        <input class="form-check-input"  checked  type="radio" name="pay-way"  id="Line-pay" value="LinePay"
                        checked={this.state.paymentWay==='LinePay'} 
                        onChange={this.onOptionChange.bind(this)}
                        />
                        <label class="form-check-label" for="Line-pay">Line Pay(TW)</label>
                      </div> */}
                      <div class="form-check form-check-inline">
                        <input class="form-check-input"  type="radio" name="pay-way"   id="other-pay" value="otherWay"
                        checked={this.state.paymentWay==='otherWay'} 
                        onChange={this.onOptionChange.bind(this)}
                        />
                        <label class="form-check-label" for="other-pay">paypel(for foreign,ect...)</label>
                      </div>
                      </div>
                      </div>
                     <div class="form-group row">
                      <label for="inputPassword" class="col-3 col-form-label text-right" >帳號末4碼</label>
                        <div class="col-8">
                          <input type="text" class="form-control" id="inputPassword" placeholder="1234" 
                          value={this.state.accountNumber}  
                          onChange={
                            (e)=>{
                              if(e.target.value.length>4){alert('數字太長啦！');return false;}
                              this.setState({accountNumber:e.target.value.replace(/[^0-9]+/g, "")})
                            }
                          }
                          />
                        </div>
                     </div>
                    <div class="form-group row">
                      <label for="inputPassword" class="col-3 col-form-label text-right">預計匯款日</label>
                      <div class="col-8" > 
                        
                        <DayPickerInput
                          dayPickerProps={{
                            month: new Date(),
                            todayButton: 'Today',
                          }}
                         onDayChange={day=>{this.setState({transferDate:day});}} 
                         />
                        </div>
                     </div>  
                    <div class="form-group row">
                      <div class="col-3"></div>
                      <div class="col-8 text-right">
                        <button type="b utton" 
                          className={styles.button} 
                          onClick={this.handleSubmit.bind(this)}
                          >送出</button>
                      </div>
                     
                    </div>
                </form>
                </div> 
            
        
                {/* <div class="about-image">
                  <img src="image/about.png" alt="" />
                </div> */}
              </div>

        </section>
        
        
  
    );
    
  }
    

}


export default MianArea;